import microBlog from "../../components/microBlog/microBlog.vue"
export default {
	components: {
		microBlog
	},
	data() {
		return {
			blog: "",
			showGroup: false
		}
	},
	onLoad() {
		//登录判断
		// var res = global.isLogin();
		var res = true
		console.log(res)
		if(!res){
		    uni.showModal({
				title:'请登录',
				content:"请登录",
				success:function(){
					if (res.confirm) {
					    console.log('用户点击确定');
						uni.navigateTo({
							url:"/pages/login/login"
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			})
		}
		uni.request({
		    url: 'https://www.fastmock.site/mock/5be1a5131b5cf07cd613ef5c49c0d5e9/blog/microBlog',
		    success: (res) => {
		        console.log("微博数据",res.data);
				this.blog = this.blog == "" ? res.data.data : this.blog.push(res.data.data)
		    },
			fail: (err) => {
				console.log("失败",err)
			}
		});
	},
	methods: {
		showGrouping() {
			this.showGroup = !this.showGroup
		}
	}
}
